* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.country {
  background-image: url(../img/background.jpg);
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.country-info {
  color: #fafafa;
  margin-left: 15px;
}
.country__title {
  display: flex;
  align-items: center;
}
.country__img {
  margin-right: 10px;
}
.country__title-name {
  text-transform: uppercase;
}

.country__languages {
  display: flex;
  align-items: baseline;
}
.country__text {
  font-weight: 500;
  margin-top: 5px;
  margin-bottom: 5px;
}
.country__languages-list {
  font-weight: 500;
  margin-left: 10px;
}
.accent {
  font-weight: 800;
}
.country-list {
  color: #fafafa;
  margin-left: 15px;
}
.country-list__info {
  display: flex;
  padding: 5px;
}
